@tailwind base;
@tailwind components;
@tailwind utilities;

body, html, #root {
  width: 100%;
  height: 100%;
}

@layer base {
  h1 {
    @apply font-josefin my-0
  }
  div {
    @apply font-lato
  }
 
}

@layer utilities {
  .no-scrollbar::-webkit-scrollbar {
    display: none;
}
}

.clip-path {
  animation: clipPath 0.7s forwards!important;
}


/*@keyframes clipPath {
  from {clip-path: inset(100% round 10rem);}
  to {clip-path: inset(0% round 0rem);}
}*/

@keyframes clipPath {
  from {clip-path: inset(0% 100% 0% 0%)}
  to {clip-path: inset(0% 0% 0% 0%)}
}
